<template>
 <div class="progress-bar" ref="progressBar" @click="progressClick">
    <div class="bar-inner">
      <div class="progress" ref="progress"></div>
      <div class="progress-btn-wrapper" ref="progressBtn"   
           @touchstart.prevent="progressTouchStart"
           @touchmove.prevent="progressTouchMove"
           @touchend="progressTouchEnd" > 
        <div class="progress-btn"></div>
      </div>
    </div>
  </div>
</template>
<script>
import  { prefixStyle } from 'common/js/dom.js';
const transform = prefixStyle('transform')
const btnWidth = 16;
export default {
     props:{
        percent:{
           type:Number,
           default:0
        }
     },
     watch:{
       percent(val){
           if(val>=0 && !this.touch.inititaed){
             const barWidth = this.$refs.progressBar.clientWidth - btnWidth
             const offsetWidth = val * barWidth
              this._offset(offsetWidth)
           }
       }
     },
     created(){
        this.touch = {}
     },
     methods:{
       progressTouchStart(e){
          this.touch.inititaed = true ;
          this.touch.startX = e.touches[0].pageX;
          this.touch.left = this.$refs.progress.clientWidth;
       },
       progressTouchMove(e){
           if(!this.touch.inititaed){
              return
           }
           const deltaX = e.touches[0].pageX - this.touch.startX;
           const offsetWidth = Math.min(this.$refs.progressBar.clientWidth-btnWidth,Math.max(0,this.touch.left+deltaX))
           this._offset(offsetWidth)
           this._triggerPecent()
       },
       progressTouchEnd(){
           this.touch.inititaed = false
       },
       progressClick(e){
           const rect = this.$refs.progressBar.getBoundingClientRect()
           const offsetWidth = e.pageX - rect.left
           this._offset(offsetWidth)
        // 这里当我们点击 progressBtn 的时候，e.offsetX 获取不对
        // this._offset(e.offsetX)
          this._triggerPecent();
       },
       _offset(offsetWidth){
           this.$refs.progress.style.width = `${offsetWidth}px`
           this.$refs.progressBtn.style[transform] = `translate3d(${offsetWidth}px,0,0)`
       },
       _triggerPecent(){
         const BarMaxWidth =  this.$refs.progressBar.clientWidth - btnWidth;
         const percent = this.$refs.progress.clientWidth/BarMaxWidth;
         this.$emit("progressPecent",percent)
       }
    },

}
</script>
<style lang="scss" scoped>
 @import "../../common/style/variable";

.progress-bar{
    height: 30px;
    .bar-inner{
      position: relative;
      top: 13px;
      height: 4px;
      background: rgba(0, 0, 0, 0.3);
      .progress{
        position: absolute;
        height: 100%;
        background: $color-theme;
      }
      .progress-btn-wrapper{
        position: absolute;
        left: -8px;
        top: -13px;
        width: 30px;
        height: 30px;
        .progress-btn{
          position: relative;
          top: 0.1rem;
          left: 0.08rem;
          box-sizing: border-box;
          width: 0.1rem;
          height: 0.1rem;
          border: 3px solid $color-text;
          border-radius: 50%;
          background: $color-theme;
        }
      }
  }
}
</style>



